/// Helper mixins for font-sizes used in conjuction with the
/// `gel-typography` library.
///
/// @example scss
///   .MyHero {
///     @include canon-bold;
///   }

@mixin canon {
  @include gel-typography('canon');
}

@mixin canon-bold {
  @include gel-typography('canon-bold');
}

